import { Upload } from 'element-ui'
import mustSign from '~/mixins/mustSign'
import upload from '~/mixins/upload'
import Editor from '~/components/editor'

export default {
  name: 'Write',
  components: {
    Editor,
    ElUpload: Upload
  },
  mixins: [mustSign, upload],
  asyncData({ app, error, query }) {
    const slug = query.slug
    if (!slug) {
      return
    }
    return app.$axios
      .$get('v1/pin/update/content', {
        params: { slug }
      })
      .then((data) => {
        return data
      })
      .catch(error)
  },
  data() {
    return {
      slug: '',
      title: {
        banner: null,
        text: ''
      },
      content: [],
      notebook: '',
      area: '',
      topic: '',
      last_edit_at: '',
      published_at: '',
      loading: false
    }
  },
  mounted() {
    if (this.$cache.has(`editor_local_draft_title-${this.slug}`)) {
      this.title = this.$cache.get(`editor_local_draft_title-${this.slug}`)
    }
  },
  methods: {
    customImageUploadSuccess(res, file) {
      this.handleImageUploadSuccess(res, file)
      const banner = res.data
      if (banner.width < 960 || banner.height < 540) {
        this.handleImageUploadRemove(file)
        this.$toast.info('图片尺寸不符合要求')
        return
      }
      this.title.banner = banner
      this.saveTitle()
    },
    onEditorSave() {
      this.saveTitle()
    },
    saveTitle() {
      this.$cache.set(`editor_local_draft_title-${this.slug}`, this.title)
    },
    deleteBanner() {
      this.title.banner = null
      this.saveTitle()
    },
    preValidate() {
      if (this.loading) {
        return true
      }
      if (!this.title.text) {
        this.$toast.info('标题不能为空')
        return true
      }
      if (!this.content.length) {
        this.$toast.info('内容不能为空')
        return true
      }
      this.loading = true
      return false
    },
    actionCreate(publish) {
      if (this.preValidate()) {
        return
      }

      this.$axios
        .$post('v1/pin/create/story', {
          notebook: this.notebook,
          content: [
            {
              type: 'title',
              data: this.title
            }
          ].concat(this.content),
          publish
        })
        .then((slug) => {
          this.removeCache()
          if (publish) {
            window.location = this.$alias.pin(slug)
          } else {
            window.location = this.$alias.create(slug)
          }
        })
        .catch((err) => {
          this.$toast.error(err.message)
          this.loading = false
        })
    },
    actionUpdate(publish) {
      if (this.preValidate()) {
        return
      }

      const { slug } = this
      this.$axios
        .$post('v1/pin/update/story', {
          slug,
          notebook: this.notebook,
          content: [
            {
              type: 'title',
              data: this.title
            }
          ].concat(this.content),
          publish
        })
        .then((slug) => {
          this.removeCache()
          if (publish) {
            window.location = this.$alias.pin(slug)
          } else {
            this.loading = false
          }
        })
        .catch((err) => {
          this.$toast.error(err.message)
          this.loading = false
        })
    },
    actionRedo() {
      if (this.content.length || this.title.text.length || this.title.banner) {
        this.removeCache()
        this.$toast.success(this.slug ? '撤销成功' : '删除成功').then(() => {
          window.location.reload()
        })
      }
    },
    removeCache() {
      this.$cache.del(`editor_local_draft_title-${this.slug}`)
      this.$cache.del(`editor_local_draft-${this.slug}`)
    }
  },
  head() {
    return {
      title: '创作中心'
    }
  }
}
